<template>
  <main>
    <div class="movies-container">
      <ul class="movie-list" v-if="fourUsers.length">
        <li v-for="(user,idx) in fourUsers" :key="idx">

          <div class="poster">
            <router-link :to="'/user/details/' + user._id">
              <img :src="user.userImg">
            </router-link>
          </div>

          <div class="details flex space-even">
            <!-- <label>Rank ({{arrayAvg(movie.rank)}}<i class="fas fa-star"></i>)</label> -->
            <label>Views (74,841)</label>
          </div>

          <!-- <div class="users">
            <li class="flex space-between align-center"
              v-for="(review,idx) in showReviews(movie._id)" :key="idx">
              <div class="user-img-wrap">
                <img @click="userLink(review.user.userId)" :src="review.user.userImg">
              </div>
              <p class="text">
                {{limitWords(review.content.txt)}}
                <span @click="userLink(review.user.userId)">Read more</span>
              </p>
              <p class="likes">LIKES ({{review.rate.countLike.length}})</p>
            </li>
          </div> -->
        </li>
      </ul>
      <div class="galery-link">
        <router-link to="/movies">
          <label>See 10 Top Users</label>
        </router-link>
      </div>
    </div>

  </main>
</template>

<script>

export default {
  name: "main1",
  data() {
    return {
    }
  },
  created() {
    if (!this.$store.state.usersModule.users.length) {
      this.$store.dispatch({ type: 'usersModule/loadUsers' });
    }
  },
  computed: {
    fourUsers() {
      return this.$store.getters['usersModule/fourUsers'];
    },
    // reviews() {
    //   if (!this.$store.state.reviewsModule.fourReviews.length) {
    //     this.fourMovies.forEach(movie => {
    //       this.$store.dispatch({ type: "reviewsModule/loadFourReviews", id: movie._id });
    //     })
    //   }
    //   return this.$store.state.reviewsModule.fourReviews;
    // }
  },
  methods: {
    showReviews(movieId) {
      var toSend = [];
      this.reviews.forEach(reviewsForMovie => {
        if (reviewsForMovie[0].movie.movieId === movieId) {
          toSend = reviewsForMovie;
        }
      })
      return toSend;
    },
    limitWords(str) {
      return str.substring(0, 45) + '...';
    },
    userLink(userId) {
      this.$router.push(`/user/details/${userId}`);
    },
    arrayAvg(likes) {
      var sum = 0;
      likes.forEach(like => sum += like);
      var avg = sum / likes.length;
      return avg.toFixed(2);
    }
  },
}
</script>

<style scoped lang="scss">
main {
  height: 100%;
  padding-top: 6vh;
  max-width: 940px;
  margin: 0 auto;
  padding-left: 10px;
  padding-right: 10px;
}
.movies-container {
  padding: 0px 0px 25px 0px;
  .galery-link {
    padding: 8px 8px 0 0;
    text-align: right;
    label {
      cursor: pointer;
      text-decoration: underline;
      color: #dbd5d5;
      &:hover {
        color: #3481b4;
      }
    }
  }
}
.movie-list {
  padding: 0;
  list-style-type: none;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  grid-gap: 20px;
  color: #dbd5d5;
  > li {
    border-radius: 4px;
    padding: 6px 2px 2px 2px;
    background-color: #151416;
    .poster {
      height: 200px;
      overflow: hidden;
      margin: 0 auto;
      img {
        width: 100%;
        height: 100%;
        // object-fit: cover;
        object-fit: contain;
        object-position: center;
        transition: transform 0.4s;
        // border-radius: 0px 55px 0px 55px;
        &:hover {
          transform: scale(1.05);
        }
      }
    }
    .details {
      padding: 6px 2px 2px 2px;
      font-size: 0.75em;
    }
    .users {
      li {
        padding: 2px 2px 2px 2px;
        .user-img-wrap {
          width: 40px;
          height: 40px;
          // height: auto;
          overflow: hidden;
          // margin: 0 auto;
          display: inline-block;
          flex: 0 0 40px;
          img {
            cursor: pointer;
            border-radius: 50%;
            width: 100%;
            height: 100%;
            object-fit: cover;
            border: 2px solid #3481b400;
            &:hover {
              border: 2px solid #3481b4;
            }
          }
        }
        .text {
          padding-left: 4px;
          text-align: left;
          margin: 0;
          font-size: 0.6em;
          span {
            cursor: pointer;
            font-weight: bold;
            color: #dbd5d5;
            &:hover {
              color: #3481b4;
            }
          }
        }
        .likes {
          text-align: center;
          margin: 0;
          font-size: 0.6em;
          padding-left: 4px;
        }
      }
    }
  }
}

.feature-list-grid {
  list-style-type: none;
  margin: 0;
  padding: 0;
  margin: 0 0 2vh 0;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  grid-gap: 20px;
  li {
    border-radius: 6px;
    background-color: rgb(18, 22, 26);
    width: 100%;
    padding: 20px;
    margin: 0 auto;
    transition: background-color 0.3s;
    cursor: pointer;
    &:hover {
      background-color: rgb(9, 11, 14);
    }
    & p {
      color: rgb(223, 223, 223);
      font-size: 0.8em;
      text-align: left;
    }
  }
}

</style>